*,
html,
body,
*::before,
*::after {
  box-sizing: inherit;
}

.colorTips {
  display: block;
  border: 5px solid dimgray;
  overflow: auto;
}

.colorTips > div {
  display: inline-block;
}

.colorTips .content {
  background-color: #4FC1E9;
}

.colorTips .padding {
  background-color: #A0D468;
}

.colorTips .padding_padding {
  background-color: #AC92EC;
}

.colorTips .border {
  background-color: #DA4453;
}

.colorTips .margin {
  background-color: #FFCE54;
}

.colorTips .anim {
  height: 350px;
}

@keyframes box-demo1 {
  0% {
    margin: 25px;
    border: 1px solid #DA4453;
    padding: 25px;
    box-sizing: auto;
    width: 0px;
    height: 80px;
    line-height: 20px;
    overflow: auto;
    background-color: #A0D468;
    animation: 15s ease-in-out 1s infinite alternate forwards box-demo1;
  }
  25% {
    margin: 25px;
    border: 20px solid #DA4453;
    padding: 20px;
    box-sizing: auto;
    width: 240px;
    height: 80px;
    line-height: 20px;
    overflow: auto;
    background-color: #AC92EC;
    animation: 15s ease-in-out 1s infinite alternate forwards box-demo1;
  }
  50% {
    margin: 25px;
    border: 30px solid #DA4453;
    padding: 0px;
    box-sizing: auto;
    width: 800px;
    height: 80px;
    line-height: 20px;
    overflow: auto;
    background-color: #4FC1E9;
    animation: 15s ease-in-out 1s infinite alternate forwards box-demo1;
  }
  75% {
    margin: 25px;
    border: 0px solid #DA4453;
    padding: 0px;
    box-sizing: auto;
    width: 500px;
    height: 80px;
    line-height: 20px;
    overflow: auto;
    background-color: #4FC1E9;
    animation: 15s ease-in-out 1s infinite alternate forwards box-demo1;
  }
  100% {
    margin: 25px;
    border: 10px solid #DA4453;
    padding: 20px;
    box-sizing: auto;
    width: 260px;
    height: 80px;
    line-height: 20px;
    overflow: auto;
    background-color: #AC92EC;
    animation: 15s ease-in-out 1s infinite alternate forwards box-demo1;
  }
}

.content-box {
  display: block;
  margin: 25px;
  border: 1px solid #DA4453;
  padding: 30px;
  box-sizing: content-box;
  width: 300px;
  height: 80px;
  line-height: 20px;
  overflow: auto;
  background-color: #DA4453;
  animation: 15s ease-in-out 1s infinite alternate forwards box-demo1;
}

.border-box {
  display: block;
  margin: 25px;
  border: 10px solid #DA4453;
  padding: 0px;
  box-sizing: border-box;
  width: 300px;
  height: 80px;
  line-height: 20px;
  overflow: auto;
  background-color: #DA4453;
  animation: 15s ease-in-out 1s infinite alternate forwards box-demo1;
}

div.parent {
  border: 12px solid gold;
}

.child1 {
  border: 1px solid blue;
}

.child2 {
  border: 1px solid red;
}

div.parent > div {
  margin: 30px 0px;
}

.child3,
.child4 {
  display: inline-block;
  border: 3px solid darkblue;
}

footer.dad-son {
  overflow: hidden;
  margin: 38px 18px;
  background-color: #AAB2BD;
  animation: 3s ease-in-out .5s infinite alternate both margin-combine;
  border-top: 18px solid #DA4453;
}

.child5 {
  border: 5px solid #DA4453;
  margin: 25px 28px #FFCE54;
  padding-bottom: 1px;
}

@keyframes margin-combine {
  0% {
    border: 0px solid gold;
    border-top: 0px solid #DA4453;
    padding-bottom: 0px;
  }
  50% {
    border: 5px solid cyan;
    border-top: 12px solid #DA4453;
    padding-bottom: 16px;
  }
  55% {
    border: 0px solid gold;
    border-top: 12px solid green;
    padding-bottom: 1px;
  }
  100% {
    border: 0px solid gold;
    border-top: 0px solid #DA4453;
    padding-bottom: 0px;
  }
}
